import React from 'react'
import Lottie from 'react-lottie';
import animationData from 'assets/lottie/OrderFailed.json'
const Failed = (props) => {
  const defaultOptions = {
		loop: false,
		autoplay: true,
		animationData: animationData,
		rendererSettings: {
		  preserveAspectRatio: 'xMidYMid slice'
		}
  };
  return (
    <div className='mainContainer'>
      <div className="dataContainer" style={{ textAlign: "center" }}>
        <Lottie options={defaultOptions} style={{marginTop: '50%',marginBottom:'10%',width:'50%' }} />
        <div className='bigHeader' style={{ margin: "4% 10%" }}>
          Order failed
        </div>
        <button className='solidButton' style={{ width: "auto", margin: "10% auto", paddingLeft: "20px", paddingRight: "20px" }}
          onClick={() => props.history.push("/Dashboard")}
        >Go to dashboard</button>
      </div>
    </div>
  )
}

export default Failed